
import { defineComponent, getCurrentInstance, onMounted, ref } from "vue";
import { GiveListConfig } from "@/types/present";
import { userStore } from "@/store/user";
import { storeToRefs } from "pinia";
export default defineComponent({
  name: "giveRecord",
  setup() {
    const { appContext } = getCurrentInstance() as any;
    const axios = appContext.config.globalProperties.$axios;
    const store = userStore();
    const { liveId } = storeToRefs(store);
    const giveRecord = ref<GiveListConfig[]>([]);
    const equipmentHeight = ref<string>(""); //设备高度
    const getGiveRecord = () => {
      axios
        .get(axios.defaults.baseURL + "/jeecg-boot/user/api/getMygivingGiftList", {
          params: {
            liveId: liveId.value,
          },
        })
        .then((res: any) => {
          if (res.data.code == 200) {
            interface Opt {
              id: number;
              giftName: string;
              liveName: string;
              amount: number;
              createTime: string;
            }
            res.data.result.forEach((item: any) => {
              const opt: Opt = {
                id: Date.now(),
                giftName: item.giftName,
                liveName: item.liveName,
                amount: item.amount,
                createTime: item.createTime,
              };
              giveRecord.value.push(opt);
            });
          }
        });
    };

    const elementHeight = () => {
      equipmentHeight.value = window.innerHeight + "px";
    };
    function handleTouch(e: any) {
      e._isScroller = true;
    }
    onMounted(async () => {
      await getGiveRecord();
      await elementHeight();
    });
    return {
      giveRecord,
      equipmentHeight,
      getGiveRecord,
      elementHeight,
      handleTouch,
    };
  },
});
